<template>
    <div class="item">
        <span class="checkbox"><input type="checkbox" @click="onClick" id="check"></span>
        <i :class="isChecked?'checked':'check'"></i>
        <span :class="isChecked?'content-del':'content'">{{todo.content}}</span>
        <i
         v-if="isChecked"
         @click="del"
         class="del"
        ></i>
        
    </div>    
</template>
<script>
export default {
    props: {
        todo: Object
    },
    data() {
        return {
            isChecked: false
        }
    },
    methods: {
        onClick(e){
            // this.isChecked = e.target.checked;
            this.isChecked = e.currentTarget.checked;

            console.log(this.isChecked);
        },
        del(){
            this.$emit('del', this.todo.id);
        }
    }
}
</script>
<style lang="scss" scoped>
* {
    text-shadow: 0 0 15px rgba(0, 0, 0, 0.13);
}
.item {
    // position: absolute;
    width: 40%;
    position: relative;
    text-align: left;
    top: 30px;
    left: 27%;
    // padding-left: 27%;
    .checkbox {
        // width: 20px; height: 20px;
        position: absolute;
        top: -1px; left: -45px;
        display: inline-block;
        // background: pink;
        z-index: 999;
    }
    input[type=checkbox] {
        opacity: 0;
        width: 50px;height: 50px;
        transform: translateY(15px);
        cursor: pointer;
    }
    .content {
        font-size: 50px;
        color: #fff;
        position: relative;
        margin-left: 30px;margin-right: 20px;
        left: -30px;
        transition: all .6s ease;
    }
    .content-del {
        text-decoration: line-through;
        font-size: 50px;
        margin-left: 30px;margin-right: 20px;
        color: #fff;
        position: relative;
        left: -30px;
        transition: all .4s ease;
    }
    .check, .checked {
        position: relative;
        left: -46px;
        top: 10px;
        z-index: 0;
        width: 50px;height: 50px;
        display: inline-block;
        background-size: 70px 70px !important;
    }
    .del {
        width: 40px;height: 40px;
        display: inline-block;
        background-size: 40px 40px !important;
    }
    .check {
        background: url('../../assets/check-box-outline-blank.png') no-repeat center center;
    }
    .checked {
        background: url('../../assets/check-box-outline.png') no-repeat center center;
    }
    .del {
        background: url('../../assets/del.png');
    }
}
</style>


