<template>
    <div class="time">
        <h1>{{now}}</h1>
    </div>    
</template>
<script>
export default {
    data(){
        return {
            now: ''
        }
    },
    created(){
        var now = new Date();
        var hh = now.getHours().toString().padStart(2, 0);
        var mm = now.getMinutes().toString().padStart(2, 0);
        this.now = hh + ':' + mm;
    }
}
</script>
<style lang="scss" scoped>
.time {
    h1 {
        font-size: 140px;
        color: #fff;
        // box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.158);
        text-shadow: 0 0 15px rgba(0, 0, 0, 0.13);
    }
}
</style>

