<template>
    <div class="todo">
        <input 
         type="text"
         placeholder="输入您的今日任务"
         @keydown.enter="addTodo"
         v-model="todo"
        >
        <!-- <p v-for="item in todoList" :key="item.id">{{item.content}}</p> -->
        <transition-group>
            <v-item
             v-for="item in todoList"
             :key="item.id"
             :todo="item"
             v-on:del="del"
             >
            </v-item>
        </transition-group>

    </div>
</template>
<script>
import item from "./subcomponents/item.vue"
export default {
    data(){
        return {
            id: 0, //初始id号
            todo: '',  //当前输入值
            todoList: []  //todo列表
        }
    },
    created(){
        var todoList = JSON.parse(localStorage.getItem("todoList"));
        if(todoList) {
            this.todoList = todoList;
        }
    },
    methods: {
        addTodo(){
            if(this.todo.trim()==''){
                return
            }
            this.todoList.unshift({
                id: this.id++,
                content: this.todo.trim(),
                completed: false
            });
            this.todo = "";
            localStorage.setItem("todoList", JSON.stringify(this.todoList));
        },
        del(id){
            this.todoList.splice(this.todoList.findIndex(function(item){
                if(item.id == id){
                    return item
                }
            }), 1);
            localStorage.setItem("todoList", JSON.stringify(this.todoList));

        }
    },
    components: {
        "v-item": item
    }
}
</script>
<style lang="scss" scoped>
* {
    text-shadow: 0 0 15px rgba(0, 0, 0, 0.13);
}
.todo {
    position: absolute;
    width: 100%;
    padding-bottom: 20px;
    input {
        border: none {
            bottom: 2px solid #fff;
        }
        background: none;
        color: #fff;
        font-size: 60px;
        width: 450px;
        outline: none;
        &::placeholder {
            color: #fafafa;
            font-size: 50px;
        }
    }
}
.v-enter,
.v-leave-to {
    opacity: 0;
}
.v-enter-active {
    transition: all 1s ease;
}
.v-leave-active {
    transition: all .5s ease;
}
.item {
    margin-top: 10px;
}
</style>

